import type { Metadata } from "next";
import localFont from "next/font/local";
import "./globals.css";
import AnimatedBackground from "@/app/components/AnimatedBackground";
import Nav from "@/app/components/Tabs/Nav";
import AnimatedLogo from "@/app/components/AnimatedLogo";
import {motion} from "framer-motion";
import {useEffect, useState} from "react";
import {usePathname} from "next/navigation";

const dirtylineFont = localFont({
    src: [
        {
            path: "../public/fonts/Dirtyline 36daysoftype 2022.woff2",
            weight: "400",
            style: "normal",
        },
        {
            path: "../public/fonts/Dirtyline 36daysoftype 2022.woff",
            weight: "400",
            style: "normal",
        },
        {
            path: "../public/fonts/Dirtyline 36daysoftype 2022.ttf",
            weight: "400",
            style: "normal",
        },
    ],
    variable: "--font-dirtyline",
    display: "swap",
});

const nohemiFont = localFont({
    src: [
        {
            path: "../public/fonts/Nohemi-Thin.woff2",
            weight: "100",
            style: "normal",
        },
        {
            path: "../public/fonts/Nohemi-Regular.woff2",
            weight: "400",
            style: "normal",
        },
        {
            path: "../public/fonts/Nohemi-Bold.woff2",
            weight: "700",
            style: "normal",
        },
        {
            path: "../public/fonts/Nohemi-ExtraBold.woff2",
            weight: "800",
            style: "normal",
        },
    ],
    variable: "--font-nohemi",
    display: "swap",
});



export const metadata: Metadata = {
  title: "Create Next App",
  description: "Generated by create next app",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {

    const [yOffset, setYOffset] = useState("46vh");
    const pathname = usePathname();
    const isRoot = pathname === "/";

    useEffect(() => {
        const updateYOffset = () => {
            if (window.innerWidth < 768) {
                setYOffset("37vh"); // Adjust for mobile
            } else {
                setYOffset("46vh"); // Default for larger screens
            }
        };

        updateYOffset(); // Set initial value
        window.addEventListener("resize", updateYOffset);

        return () => window.removeEventListener("resize", updateYOffset);
    }, []);

  return (
    <html lang="en">
    <body className={`${dirtylineFont.variable} ${nohemiFont.variable} antialiased`}>
    {/* Background for all pages */}
    <AnimatedBackground/>

    <motion.div
        initial={!isRoot ? {scale: 0.4, y: yOffset} : {scale: 1, y: 0}}
        animate={!isRoot ? {scale: 0.4, y: yOffset} : {scale: 1, y: 0}}
        transition={{duration: 1.2, ease: "easeInOut"}}
    >
        <div
            className="flex items-center justify-center min-h-screen scale-50 m:scale-50 lg:scale-100 cursor-pointer">
            <AnimatedLogo/>
        </div>

    </motion.div>

    {/* NAV */}
    <Nav/>

    <main>{children}</main>
    </body>
    </html>
  );
}
